import { List } from 'antd';

export type TUser = {
  did: string;
  name: string;
  certificationName: string;
  certificatedAte: string;
  expiredAt: string;
  issuerName: string;
  transactionHash: string;
  imageChecksum: string;
};

type TProps = {
  user: TUser;
}

export default function Profile({ user }: TProps) {

  return (
    <List
    itemLayout="horizontal"
    dataSource={[{}, {}]}
    renderItem={item => (
      <List.Item>
        <div>
        <span className="name">Certification: </span><span>{user.name}</span>
      </div>
      </List.Item>
    )}
  />
  );
}
